/*-----------------------------------------------
|   Theme Styles
-----------------------------------------------*/
/*-----------------------------------------------
|   Reboot
-----------------------------------------------*/
::placeholder {
  opacity: 1;
  color: var(--falcon-gray-400);
}

::selection {
  background-color: var(--falcon-primary);
  color: #fff;
}

[id] {
  scroll-margin-top: calc(var(--falcon-top-nav-height) + 1.5rem);
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

pre,
code {
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
}

section {
  position: relative;
  padding-top: 4rem;
  padding-bottom: 4rem;
}
@media (min-width: 992px) {
  section {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
  }
}

input,
button,
select,
optgroup,
textarea,
label,
.alert,
.badge,
.blockquote-footer,
.btn,
[data-list] .page,
.table-list .page,
.navbar,
.pagination,
.valid-feedback,
.invalid-feedback {
  font-family: var(--falcon-font-sans-serif);
}

pre {
  margin: 0;
  transform: translate3d(0, 0, 0);
}

strong {
  font-weight: 600;
}

label {
  font-size: 0.8333333333rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  letter-spacing: 0.02em;
}

pre,
code,
kbd,
samp {
  font-size: 0.875rem;
}

[dir=rtl] pre, [dir=rtl] code, [dir=rtl] kbd, [dir=rtl] samp {
  direction: rtl;
}

[data-bs-theme=dark] .d-dark-none {
  display: none;
}

html:not([data-bs-theme=dark]) .d-light-none {
  display: none;
}

/* -------------------------------------------------------------------------- */
/*                                   Navbar                                   */
/* -------------------------------------------------------------------------- */
:root {
  --falcon-top-nav-height: 4.3125rem;
}
@media (min-width: 992px) {
  :root.double-top-nav-layout {
    --falcon-top-nav-height: 8.688rem;
  }
}

.navbar .nav-link {
  outline: none;
}
.navbar .dropdown-menu {
  overflow: visible;
  left: -0.5625rem;
}
.navbar .dropdown-menu.dropdown-caret:after {
  content: "";
  position: absolute;
  z-index: -1;
  width: 1rem;
  height: 1rem;
  background: #fff;
  background: inherit;
  top: -0.375rem;
  left: 1.8rem;
  transform: rotate(45deg);
  border-radius: 0.125rem;
  border-width: 1px 0 0 1px;
  border-style: solid;
  border-color: var(--falcon-border-color);
